import React from 'react';
import reactMixin from 'react-mixin';
import axios from 'axios';
import store from 'store';
import { withRouter, Redirect } from 'react-router-dom';
import { Button, WhiteSpace, WingBlank, List, InputItem, Modal } from 'antd-mobile';
import Logo from '../../components/Logo/index';
import mixins from '../..//mixins/index';

let showAlert = ({ title = '', context = '' }) => {
    let alertInstance = Modal.alert(title, context, [
        { text: 'OK', onPress: () => { } },
    ]);
    setTimeout(() => {
        // closeclose
        alertInstance.close();
    }, 2000);
}

@withRouter
@reactMixin.decorate(mixins)
class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            password: ""
        }
        this.register = this.register.bind(this);
    }
    register() {
        this.props.history.push("/register");
    }
    handleChange(key, val) {
        this.setState({
            [key]: val
        });
    }
    Login() {
        let _this = this;
        let { name, password } = this.state;
        if (name === "") {
            showAlert({ context: "" });
            return
        }
        if (password === "") {
            showAlert({ context: "" });
            return
        }
        axios.post(this.props._Global.config.apiBasicUrl + this.props._Global.api.login.login, { name, password }).then((res) => {
            if (res.code === "0") {
                //
                store.set('userInfo', res.data);
                if (res.data.role === "boss") {
                    _this.props.history.push("/main/bossiofo");
                } else {
                    _this.props.history.push("/main/genuisinfo");
                }
            } else {
                showAlert({ context: '' });
            }
        });
    }

    render() {
        let url = this.props._isAuth()
        return url ? (<Redirect from="/login" to={ url }></Redirect>) : (
            <React.Fragment>
                <Logo></Logo>
                <WhiteSpace />
                <List>
                    <InputItem onChange={(v) => this.handleChange("name", v)}></InputItem>
                    <InputItem onChange={(v) => this.handleChange("password", v)} type="password"></InputItem>
                </List>
                <WhiteSpace />
                <WingBlank>
                    <Button type="primary" onClick={() => this.Login()}></Button><WhiteSpace />
                    <Button type="primary" onClick={this.register}></Button>
                </WingBlank>
            </React.Fragment>
        )
    }
}

export default Login;


